.info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.recipe-num,
.follower-num,
.favorite-num {
  width: 100%;
  text-align: center;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}
.recipe-num > .number,
.follower-num > .number,
.favorite-num > .number {
  font-size: 2em;
  margin-left: 10px;
}

.recipe-num span:last-child,
.follower-num span:last-child,
.favorite-num span:last-child {
  font-size: 1em;
  color: gray;
}


