div.title-container {
  margin-top: 4em;
}
.recipe-title p {
  font-size: 3em;
  font-weight: 400;
  letter-spacing: 1px;
}
.create-recipe-container {
  height: 300px;
  margin-top: 2em !important;
  max-width: 80% !important;
  margin-left: auto !important;
  margin-right: auto !important;
}

.recipe-form > div {
  margin-top: 5px;
  margin-bottom: 5px;
}

.ingredients-container {
  height: 300px;
}

.instructions-container {
  height: 300px;
}

.picture {
  height: 100%;
}

.description-text {
  padding: 20px;
}

div.title-container,
div.info {
  height: initial;
}

.container {
  width: 100%;
  height: 100%;
}

.source-url {
  font-size: .7em;
  color: gray;
}


